import {
    catchError, retryWhen, switchMap, finalize, filter, debounceTime,
    startWith, tap, scan, concatMap, takeWhile, distinctUntilChanged, map
} from 'rxjs/operators';
import { Subject, throwError, of, Observable } from 'rxjs';
import { RestData } from './rest-data.model';
import { Page } from './rest-data-page.model';

export const restDataErrorStrategy = ({
    restData = null,
    message = '',
    onError = null,
    onRetry = null,
    onCancel = null
}: {
    restData?: RestData<any>;
    message?: string;
    onError?: (error: any) => void;
    onRetry?: () => void;
    onCancel?: () => void;
} = {}) => <T>(source: Observable<T>) =>
     source.pipe(
        // In case of errors -> stop loading and display the given error message
        catchError((err) => {
            console.error(err);
            restData.loading = false;
            restData.error = message;
            if (onError) { onError(err); }
            return throwError(err);
        }),

        // Use a retry strategy based on user action (ie retry or cancel)
        retryWhen((errors) => {
            if (restData.retry$) {
                restData.retry$.unsubscribe();
            }
            restData.retry$ = new Subject();
            return errors.pipe(
                // Wait for the user to either retry or cancel the action
                switchMap(() => restData.retry$.asObservable()),

                // In case the user choose to retry -> return true and let retryWhen do its job
                // In case the user choose to cancel -> throw an error in order to exit retryWhen
                switchMap((shouldRetry: boolean) => {
                    if (shouldRetry) {
                        restData.error = undefined;
                        restData.loading = true;
                        if (onRetry) { onRetry(); }
                        return of(true);
                    }
                    if (onCancel) { onCancel(); }
                    return throwError(errors);
                }),

                // In case we were exiting retryWhen -> catch the error that helped us exit to make sure it's not printed out in console
                catchError((err) => of(err)),

                // When exiting -> make sure we don't make any leaks
                finalize(() => restData.retry$.complete())
            );
        })
    );


export const restDataInfiniteScrollStrategy = ({
    restData = null,
    errorMessage = '',
    onLoadMore = null,
    onPageLoaded = null,
    onError = null,
    onRetry = null,
    onCancel = null
}: {
    restData?: RestData<any>;
    errorMessage?: string;
    onLoadMore?: (pageIndex: number) => Observable<any>;
    onPageLoaded?: (page: Page<any>) => void;
    onError?: (error: any) => void;
    onRetry?: () => void;
    onCancel?: () => void;
} = {}) => <T>(source: Observable<CustomEvent>) => {
    // Initialize
    restData.setValue([]);

    return source.pipe(
        // Continue only when the scroll is around 200px near the bottom
        filter((event: CustomEvent) => (event.target !== undefined)),
        map((event: CustomEvent) => event.target as HTMLElement),
        map((el: HTMLElement) => (el.scrollHeight - el.scrollTop - el.clientHeight)),
        filter((scrollTop) => scrollTop <= 200),
        distinctUntilChanged(),
        // Continue only when the user stops scrolling for 200ms
        debounceTime(100),
        // Starts the stream automatically with a (pageIndex = 0)
        startWith(0),
        // Initialize RestData object
        tap(() => restData.isLoading(true).useRetry(true)),
        // Accumulate a current page index (starts with 0)
        scan((currentPageIndex) => currentPageIndex + 1, -1),
        // Get the page for the current page index
        concatMap((pageIndex: number) => onLoadMore(pageIndex)),
        // Watch for any errors
        restDataErrorStrategy({
            restData,
            message: errorMessage,
            onRetry: () => {
                /** we have to reset the data as "retry" will ask pages from scratch (ie page=0) */
                restData.setValue([]);
                if (onRetry) { onRetry(); }
            },
            onError: (error: any) => {
                if (onError) { onError(error); }
            },
            onCancel: () => {
                if (onCancel) { onCancel(); }
            }
        }),
        // Accumulate the data
        tap((page: Page<any>) => {
            restData.isLoading(false);
            if (page && page.content && page.content.length) {
                restData.setValue([...restData.value, ...page.content]);
            }
            if (onPageLoaded) { onPageLoaded(page); }
        }),
        // Take the observable live until we get the last page
        takeWhile((page: Page<any>) => (page.last !== true)),
        // Finalize
        finalize(() => restData.isLoading(false))
    );
};
