import { Observable } from 'rxjs';
import { MainLayoutStore } from '../store/main-layout.store';

export abstract class LayoutPage {

    constructor(
      private mainLayoutStore: MainLayoutStore) {
        mainLayoutStore.setBackLinkPath(this.getBackLinkPath());
    }

    protected getBackLinkPath(): string { return null;}

    protected updatePageTitle(pageTitle: string) {
        // Use setTimeout to prevent ExpressionChangedAfterItHasBeenCheckedError
        setTimeout(() => this.mainLayoutStore.setPageTitle(pageTitle), 0);
    }

    protected subscribePageTitle(pageTitle$: Observable<string>): void {
        pageTitle$.subscribe(val => this.updatePageTitle(val));
    }

}
