import { Subject, Observable } from 'rxjs';

export class RestData<T> {
    value: T;
    error: any;
    loading: boolean;
    retryEnabled: boolean;
    cancelEnabled: boolean;
    retry$: Subject<boolean>;

    // ---- PUBLIC API(s) ----

    public isLoading(value: boolean): RestData<T> {
        this.error = undefined;
        this.loading = value;
        return this;
    }

    public useRetry(value: boolean): RestData<T> {
        this.retryEnabled = value;
        this.cancelEnabled = false;
        return this;
    }

    public useRetryCancel(value: boolean): RestData<T> {
        this.retryEnabled = value;
        this.cancelEnabled = true;
        return this;
    }

    public setValue(value: T): RestData<T> {
        this.error = undefined;
        this.value = value;
        return this;
    }

    public setError(value: string): RestData<T> {
        this.loading = false;
        this.error = value;
        return this;
    }

    public retry(): void {
        if (this.retry$ && !this.retry$.closed) {
            this.retry$.next(true);
        }
    }

    public cancel(): void {
        this.error = undefined;
        if (this.retry$ && !this.retry$.closed) {
            this.retry$.next(false);
        }
    }
}
